import React, { useState } from "react";
import styles from "./Paginator.module.css";
import cn from "classnames";
import { NavLink } from "react-router-dom";
import { onTop } from "./LoadMore";

export const Paginator = ({
  productsQuantity,
  pageSize,
  currentPage,
  truePage,
  onPageChanged,
  info,
  apiCategory,
  categoryQuery,
  category,
  category2,
  portionSize = 1 //–∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å—Ç—Ä–∞–Ω–∏—Ü –≤ –ø–æ—Ä—Ü–∏–∏
}) => {
  let pagesCount = Math.ceil(productsQuantity / pageSize); //–∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å—Ç—Ä–∞–Ω–∏—Ü –≤—Å–µ–≥–æ –∏ –Ω–æ–º–µ—Ä –ø–æ—Å–ª–µ–¥–Ω–µ–π —Å—Ç—Ä–∞–Ω–∏—Ü—ã

  let pages = [];
  for (let i = 1; i <= pagesCount; i++) {
    pages.push(i);
  }

  let portionCount = Math.ceil(pagesCount / portionSize); // –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –ø–æ—Ä—Ü–∏–π
  let [portionNumber, setPortionNumber] = useState(1); // –Ω–æ–º–µ—Ä –ø–æ—Ä—Ü–∏–∏ –Ω–∞—á–∞–ª—å–Ω—ã–π –ª–æ–∫–∞–ª—å–Ω—ã–π —Å—Ç–µ–π—Ç

  let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1; //–∫—Ä–∞–π–Ω—è—è —Ü–∏—Ñ—Ä–∞ –ø–æ—Ä—Ü–∏–∏ —Å–ª–µ–≤–∞
  let rightPortionPageNumber = portionNumber * portionSize; //–∫—Ä–∞–π–Ω—è—è —Ü–∏—Ñ—Ä–∞ –ø–æ—Ä—Ü–∏–∏ —Å–ø—Ä–∞–≤–∞

  const handleClickNext = e => {
    +currentPage === +pagesCount && e.preventDefault();
  };
  const handleClickPrev = e => {
    +currentPage <= 1 && e.preventDefault();
  };

  return (
    <div className={styles.paginator}>
      {info === 1 && (
        <span style={{ margin: "40px" }}>
          ProductsQuantity {productsQuantity} Pages {pagesCount}
        </span>
      )}

      {/* prev —Å—Ç—Ä–µ–ª–∫–∞ < */}
      <NavLink
        onClick={e => {
          handleClickPrev(e);
          onTop(450);
          onPageChanged(+currentPage - 1);
        }}
        // to={(category===undefined)&&`/products/${category='all'}/filter?${categoryQuery}&startPage=${+currentPage -
        //   1}&perPage=${pageSize}`}

        to={
          ((category === undefined ||
            category === "filter" ||
            category === "all_categories") &&
            (category = "all_categories") &&
            `/products/${category}/filter?${categoryQuery}&startPage=${+currentPage -
              1}&perPage=${pageSize}`) ||
          (category !== undefined &&
            `/categories/${category}/filter?${categoryQuery}${category2}&startPage=${+currentPage -
              1}&perPage=${pageSize}`)
        }
      >
        {portionNumber > 1 &&
          currentPage < leftPortionPageNumber &&
          setPortionNumber(portionNumber - 1)}
        <span
          className={cn(
            {
              [styles.active]: currentPage > 1
            },
            styles.unactive,
            styles.arrow,
            styles.prev
          )}
        ></span>
      </NavLink>

      {/* –ø–µ—Ä–≤–∞—è —Å—Ç—Ä–∞–Ω–∏—Ü–∞ */}
      <NavLink
        to={
          ((category === undefined ||
            category === "filter" ||
            category === "all_categories") &&
            (category = "all_categories") &&
            `/products/${category}/filter?${categoryQuery}&startPage=${1}&perPage=${pageSize}`) ||
          (category !== undefined &&
            `/categories/${category}/filter?${categoryQuery}${category2}&startPage=${1}&perPage=${pageSize}`)
        }
        onClick={e => {
          onTop(450);
        }}
      >
        <span
          className={cn(
            {
              [styles.removed]: currentPage <= "2",
              [styles.added]: currentPage <= "2"
            },
            styles.page_Number
          )}
          onClick={e => {
            onPageChanged("1"); //–ø–µ—Ä–µ–¥–∞–µ–º –≤ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –∏ –∑–∞–≥—Ä—É–∂–∞–µ–º –Ω—É–∂–Ω—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É
          }}
        >
          {1}
        </span>
      </NavLink>

      {/* —Ç—Ä–∏ —Ç–æ—á–∫–∏ ... */}
      <NavLink
        to={
          ((category === undefined ||
            category === "filter" ||
            category === "all_categories") &&
            (category = "all_categories") &&
            `/products/${category}/filter?${categoryQuery}&startPage=${+currentPage -
              2}&perPage=${pageSize}`) ||
          (category !== undefined &&
            `/categories/${category}/filter?${categoryQuery}${category2}&startPage=${currentPage -
              2}&perPage=${pageSize}`)
        }
        onClick={e => {
          onTop(450);
        }}
      >
        <span
          className={cn(
            {
              [styles.removed]: currentPage <= "3"
              // [styles.added]: currentPage <='3',
            },
            styles.page_Number
          )}
          onClick={e => {
            onPageChanged(currentPage - 3); //–ø–µ—Ä–µ–¥–∞–µ–º –≤ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –∏ –∑–∞–≥—Ä—É–∂–∞–µ–º –Ω—É–∂–Ω—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É
          }}
        >
          . .
        </span>
      </NavLink>

      {/* –æ–±—ã—á–Ω—ã–µ —Å—Ç—Ä–∞–Ω–∏—Ü—ã */}
      {pages
        .filter(
          pageNumber =>
            pageNumber + 1 >= leftPortionPageNumber &&
            pageNumber - 1 <= rightPortionPageNumber
        )
        .map(pageNumber => {
          return (
            <NavLink
              to={
                ((category === undefined ||
                  category === "filter" ||
                  category === "all_categories") &&
                  (category = "all_categories") &&
                  `/products/${category}/filter?${categoryQuery}&startPage=${pageNumber}&perPage=${pageSize}`) ||
                (category !== undefined &&
                  `/categories/${category}/filter?${categoryQuery}${category2}&startPage=${pageNumber}&perPage=${pageSize}`)
              }
              key={pageNumber}
              onClick={e => {
                onTop(450);
              }}
            >
              {portionNumber > 1 &&
                currentPage < leftPortionPageNumber &&
                setPortionNumber(portionNumber - 1)}
              {portionCount > portionNumber &&
                currentPage > rightPortionPageNumber &&
                setPortionNumber(portionNumber + 1)}
              <span
                className={cn(
                  {
                    [styles.selected_Page]: currentPage === pageNumber
                  },
                  styles.page_Number
                )}
                onClick={e => {
                  onPageChanged(pageNumber); //–ø–µ—Ä–µ–¥–∞–µ–º –≤ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –∏ –∑–∞–≥—Ä—É–∂–∞–µ–º –Ω—É–∂–Ω—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É
                }}
              >
                {pageNumber}
              </span>
            </NavLink>
          );
        })}

      {/* —Ç—Ä–∏ —Ç–æ—á–∫–∏ ... */}
      <NavLink
        to={
          ((category === undefined ||
            category === "filter" ||
            category === "all_categories") &&
            (category = "all_categories") &&
            `/products/${category}/filter?${categoryQuery}&startPage=${currentPage +
              3}&perPage=${pageSize}`) ||
          (category !== undefined &&
            `/categories/${category}/filter?${categoryQuery}${category2}&startPage=${currentPage +
              3}&perPage=${pageSize}`)
        }
        onClick={e => {
          onTop(450);
        }}
      >
        <span
          className={cn(
            {
              [styles.removed]: currentPage >= pagesCount - 2
              // [styles.added]: currentPage <='3',
            },
            styles.page_Number
          )}
          onClick={e => {
            onPageChanged(currentPage + 2); //–ø–µ—Ä–µ–¥–∞–µ–º –≤ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –∏ –∑–∞–≥—Ä—É–∂–∞–µ–º –Ω—É–∂–Ω—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É
          }}
        >
          . .
        </span>
      </NavLink>
      {/* –ø–æ—Å–ª–µ–¥–Ω—è—è —Å—Ç—Ä–∞–Ω–∏—Ü–∞ */}
      <NavLink
        to={
          ((category === undefined ||
            category === "filter" ||
            category === "all_categories") &&
            (category = "all_categories") &&
            `/products/${category}/filter?${categoryQuery}&startPage=${pagesCount}&perPage=${pageSize}`) ||
          (category !== undefined &&
            `/categories/${category}/filter?${categoryQuery}${category2}&startPage=${pagesCount}&perPage=${pageSize}`)
        }
        onClick={e => {
          onTop(450);
        }}
      >
        <span
          className={cn(
            {
              [styles.removed]: currentPage >= pagesCount - 1
            },
            styles.page_Number
            // styles.unactive
          )}
          onClick={e => {
            onPageChanged(pagesCount); //–ø–µ—Ä–µ–¥–∞–µ–º –≤ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –∏ –∑–∞–≥—Ä—É–∂–∞–µ–º –Ω—É–∂–Ω—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É
          }}
        >
          {pagesCount}
        </span>
      </NavLink>

      {/* next —Å—Ç—Ä–µ–ª–∫–∞ > */}
      <NavLink
        onClick={e => {
          handleClickNext(e);
          onTop(450);
          onPageChanged(+currentPage + 1);
        }}
        // to={(category===undefined)&&(category='')&&`/products/${category}/filter?${categoryQuery}&startPage=${+currentPage -
        //   1}&perPage=${pageSize}`}

        to={
          ((category === undefined ||
            category === "filter" ||
            category === "all_categories") &&
            (category = "all_categories") &&
            `/products/${category}/filter?${categoryQuery}&startPage=${currentPage +
              1}&perPage=${pageSize}`) ||
          (category !== undefined &&
            `/categories/${category}/filter?${categoryQuery}${category2}&startPage=${+currentPage +
              1}&perPage=${pageSize}`)
        }
      >
        {portionCount > portionNumber &&
          currentPage > rightPortionPageNumber &&
          setPortionNumber(portionNumber + 1)}
        <span
          className={cn(
            {
              [styles.active]: +currentPage < +pagesCount
            },
            styles.unactive,
            styles.arrow,
            styles.next
          )}
        ></span>
      </NavLink>

      {/* <span style={{ margin: "40px" }}>
        —Ç–æ–≤–∞—Ä–æ–≤ {productsQuantity} —Å—Ç—Ä–∞–Ω–∏—Ü {pagesCount}
      </span> */}
    </div>
  );
};
