import React, { useEffect } from "react";
import { useParams, useLocation, withRouter } from "react-router";
import { connect } from "react-redux";
import querystring from "query-string";
import { Redirect } from "react-router-dom";
import {
  setCurrentPage,
  useRequestProducts,
  useMoreProducts
} from "../../store/productsReducer";
import { ProductsPagination } from "./Pagination";
// import Preloader from "../common/Preloader/Preloader";
// import { ScrollToTopController } from "./LoadMore";
import { compose } from "redux";
import {
  getCurrentPage,
  getPageSize,
  getTotalProductsCount,
  getProducts,
  moreProducts
} from "./users-selectors";

const ProductsContainer = props => {
  // console.log(props);
  // const { currentPage, pageSize } = props;
  const { currentPage, pageSize } = props;
  let location = useLocation();
  let path = `filter${location.search}`;
  // console.log("TCL: path", path);

  let { category } = useParams();
  console.log("TCL: category", category);
  const queryString = [];
  for (let key in props.filters) {
    props.filters[key].length &&
      queryString.push(`${key}=${props.filters[key].join(",")}`);
  }

  const query = querystring.stringify(props.filters, { arrayFormat: "comma" });
  const categoryQuery = `${query}`;
  console.log("TCL: categoryQuery", categoryQuery);
  const category2 = `&categories=${category}`;
  // let category3
  const apiCategory = categoryQuery + category2;

  // console.log("TCL: apiCategory", apiCategory);

  const queryString2 = require("query-string");
  const parsed = queryString2.parse(location.search);
  const truePage = parsed.startPage;

  let truePage2;
  (!truePage && (truePage2 = +currentPage)) ||
    (truePage > 0 && (truePage2 = +truePage)); //—á—Ç–æ–±—ã c –ø–µ—Ä–≤–æ–π –∑–∞–≥—Ä—É–∑–∫–∏ /pagin –∞–∫—Ç–∏–≤–Ω–∞ 1—è —Å—Ç—Ä–∞–Ω–∏—Ü–∞

  query && query.length > 0 && (truePage2 = 1); // —á—Ç–æ–±—ã –ø—Ä–∏ –∞–∫—Ç–∏–≤–∞—Ü–∏–∏ —Ñ–∏–ª—å—Ç—Ä–æ–≤ —Å–±—Ä–∞—Å—ã–≤–∞–ª–æ –Ω–∞ 1—é —Å—Ç—Ä–∞–Ω–∏—Ü—É

  useEffect(() => {
    //–ø–µ—Ä–≤–∞—è –∑–∞–≥—Ä—É–∑–∫–∞ –æ—Ç–∫—Ä–æ–µ—Ç—Å—è, –∏ –Ω–æ–º–µ—Ä —Å—Ç—Ä–∞–Ω–∏—Ü—ã –≤ —É—Ä–ª–µ, –∏ —Ä–∞–±–æ—Ç–∞–µ—Ç –Ω–∞–∑–∞–¥ –≤–ø–µ—Ä–µ–¥
    props.getProducts(
      truePage2,
      pageSize,
      categoryQuery,
      apiCategory,
      category2
    );
  }, [truePage2, query]);

  const onPageChanged = pageNumber => {
    // –∏–∑ –ø–∞–≥–∏–Ω–∞—Ç–æ—Ä–∞
    const { pageSize } = props;
    props.getProducts(
      pageNumber,
      pageSize,
      categoryQuery,
      apiCategory,
      category2
    );
  };
  let truePage3 = +currentPage + 1;
  // console.log(truePage3);

  const onLoadMore = truePage3 => {
    // –º–æ–∂–Ω–æ pageNumber –∏–∑ –ø–∞–≥–∏–Ω–∞—Ç–æ—Ä–∞
    const { pageSize } = props;
    props.moreProducts(
      truePage3,
      pageSize,
      categoryQuery,
      apiCategory,
      category2
    );
  };
  let all_categories;
  return (
    <>
      {/* {this.props.isFetching ? <Preloader/> : null} */}
      {/* –ø—Ä–∏ –≤—ã–±–æ—Ä–µ —Ñ–∏–ª—å—Ç—Ä–∞ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –Ω–∞ –ø–µ—Ä–≤—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É */}
      {/* {category===undefined&&(category8='/')&&
      <Redirect
        to={`/products${category8}filter?${categoryQuery}&startPage=${truePage2}&perPage=${pageSize}`}
      />} */}
      {/* {category!==undefined&&<Redirect
        to={`/categories/${category}/filter?${categoryQuery}${category2}&startPage=${truePage2}&perPage=${pageSize}`}
      />}
       {(category===undefined)||(category==='filter')&&(category='category')&&<Redirect
        to={`/products/${category}/filter?${categoryQuery}${category2}&startPage=${truePage2}&perPage=${pageSize}`}
      />} */}

      <Redirect
        to={
          ((category === undefined || category === "filter") &&
            (all_categories = "all_categories") &&
            `/products/${all_categories}/filter?${categoryQuery}&startPage=${truePage2}&perPage=${pageSize}`) ||
          (category !== undefined &&
            `/categories/${category}/filter?${categoryQuery}${category2}&startPage=${truePage2}&perPage=${pageSize}`)
        }
      />

      <ProductsPagination
        style={{ width: "60%" }}
        productsQuantity={props.productsQuantity}
        pageSize={props.pageSize}
        currentPage={props.currentPage}
        onPageChanged={onPageChanged}
        onLoadMore={onLoadMore}
        products={props.products}
        parsed={parsed}
        categoryQuery={categoryQuery}
        category={category}
        apiCategory={apiCategory}
        truePage={truePage}
      />

      {/* <ScrollToTopController parsed={parsed}/> */}
    </>
  );
};

let mapStateToProps = (state, categoryQuery, apiCategory, category2, query) => {
  return {
    category2: category2,
    apiCategory: apiCategory,
    categoryQuery: categoryQuery,
    query: query,
    products: getProducts(state),
    products: moreProducts(state),
    pageSize: getPageSize(state),
    productsQuantity: getTotalProductsCount(state),
    currentPage: getCurrentPage(state),
    filters: state.filters.selFilters
  };
};

let UrlProductsContainer = withRouter(ProductsContainer);
export default compose(
  connect(mapStateToProps, {
    setCurrentPage,
    getProducts: useRequestProducts,
    moreProducts: useMoreProducts
  }) //mapDispatchToProps
)(UrlProductsContainer);
